/* common part */
.container {
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
    padding-top: 130px;
}

/* left part */
.container-left {
    grid-column: 2;
}

.container-left .profile-pic {
    padding: 40px 100px;
    width: 300px;
    height: 300px;
    border-radius: 50%;
}

/* right part */
.container-right {
    grid-column: span 3;
}

.container-right hr {
    width: 40px;
    margin-left: 0px;
    border: 1px solid #0916ee;
}

.container-right .introduction h1 span {
    color: #0916ee;
}

.container-right .description {
    display: grid;
    font-size: 20px;
    margin-top: 30px;
}

.container-right .description span {
    display: flex;
    align-items: center;
    color: #2b2c2b;
}

.container-right .description .point-one, .point-two, .point-three {
    font-size: 32px;
}

.container-right .description .point-one {
    color: #ff9800; 
}

.container-right .description .point-two {
    color: #1830e4;
}

.container-right .description .point-three {
    color: #ee1212;
}

.container-right .buttons {
    margin: 50px 0px;
}

.container-right .buttons a {
    color: black;
    text-align: center;
    padding: 10px 15px;
    text-decoration: none;
}

.container-right .buttons .contact-me {
    color: #ffff;
    background-color: #5059ea;
    border-radius: 20px;
    font-weight: bold;
}

/* mobile screen */
@media screen and (max-width: 500px) {
    .container {
        width: 100%;
        display: block;
        text-align: left;
        margin-top: 40px;
    }

    .container-left {
        padding: 5px;
        width: 100%;
    }

    .container-left .my-photo {
        padding: 63px;
        width: 250px;
        height: 250px;
        border-radius: 50%;
    }

    .container-right {
        padding: 5px;
        width: 100%;
    }
}