/* common */
.header {
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
    background-color: #fff;
    grid-auto-rows: minmax(150px, auto);
    align-items: center;
}

.header a {
    color: black;
    padding: 8px 15px;
    text-decoration: none;
}

/* left part */
.header-left {
    grid-column: 2;
    display: flex;
}

.header-left .coffee-icon {
    padding: 12px;
    border-radius: 50%;
    background-color: #5059ea;
    color: #ffffff;
}

.header-left .site-title {
    font-size: 18px;
    font-weight: bold;
}

.header-left .site-title span {
    color: #0916ee;
}

/* middle part */
.header-middle {
    grid-column: 3;
}

.header-middle .active {
    color: #0916ee;
}

.header-middle a:hover {
    background-color: #d7d8e4;
    color: #000 !important;
    border-radius: 20px;
}

/* right part */
.header-right {
    grid-column: 4;
}

.header-right .resume {
    background-color: #5059ea;
    color: #ffffff;
    border-radius: 20px;
}

.header-right .resume .download-icon {
    color: #ffffff;
}

.header-right .hamburger-icon {
    display: none;
}
  
/* mobile screen */
@media screen and (max-width: 500px) {
    .header {
        display: flex;
        margin-top: 40px;
        font-size: 25px;
        justify-content: space-between;
    }

    .header a {
      float: none;
      display: block;
      text-align: left;
    }
    .header-middle a {
      display: none;
    }

    .header-left {
        font-size: 25px;
        display: contents;
    }

    .header-middle {
        width: 50%;
        align-items: end;
    }

    .header-left i.coffee-icon {
        border-radius: 50%;
        background-color: #5059ea;
    }

    .header-middle .hamburger {
        display: block;
        padding: 10px;
        margin-left: 120px;
    }

    .header-left a.title {
        font-size: 25px;
        font-weight: bold;
    }

    .header-middle .resume svg {
        color: #ffffff;
    }

    .header-left svg {
        color: #ffffff;
        padding: 5px;
    }
}