.footer {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
    position: absolute;
    bottom: 0px;
    align-items: center;
    background-color: #f3f4f6;
}

.footer .social-platform {
    grid-column: 3;
    display: flex;
    flex-direction: row;
    gap: 10px;
    font-size: x-large;
}

.footer .social-platform .linkedin {
    color: #0077b5;
}

.footer .social-platform .github {
    color: #2b3137;
}

.footer .social-platform .facebook {
    color: #1877f2;
}

.footer .social-platform .instagram {
    color: #cd486b;
}